s, aumento en el consumo energético y el desgaste de componentes o piezas. Así mismo, el polvo acumulado puede llevar a errores en el uso de periféricos de un equipo debido a problemas de lectura y a cortocircuitos. Por todo lo anterior se recomienda que un aparato electrónico sea limpiado por lo menos dos veces al año en condiciones normales y más de dos veces en condiciones en donde el entorno de trabajo sea muy agresivo (acumulación de polvo excesivo). ")])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Lubricación"}},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_c('div',{staticClass:"col-md-3 mb-4 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/t2/f4.png"),"alt":""}})])]),_c('div',{staticClass:"col-md-6"},[_c('p',[_vm._v("La lubricación dentro del mantenimiento preventivo resulta crucial en aquellos procesos en donde dos piezas siempre están en contacto, esto gracias a que el lubricante crea una película protectora sobre cada elemento. Esta intervención busca disminuir la fricción de las piezas y por ende el desgaste. Adicionalmente, la lubricación ayuda a reducir las pérdidas energéticas, disipar calor y aumentar la vida útil de los equipos. Dada la importancia de este proceso en la maquinaria de una empresa se debe prestar mucha atención a los cronogramas y recomendaciones del fabricante para evitar daños o fallas cruciales (LEVSA, s.f). Otro beneficio que posee la lubricación es la remoción de artefactos o contaminantes que puedan estar presentes a la hora de realizar un proceso de manera adicional y dependiendo del tipo de lubricación, sustancia a aplicar o mecanismo a lubricar se deben revisar los filtros de aceite para cambiarlos o limpiarlos, dado que es en estos en donde se depositan todas las impurezas o residuos “encontrados” por el aceite.")])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Recomendaciones"}},[_c('div',{staticClass:"row justify-content-around align-items